import configService from '@/common/service/config.service.js';
import store from '@/store/index.js';
class socket {
	constructor(options) {
		this.socketUrl = configService.apiUrl;
		this.socketStart = false;
		this.monitorSocketError();
		this.monitorSocketClose();
		this.socketReceive();
	}
	init(socket_type,callback) {
		const _this = this;
		if (configService.apiUrl) {
			if(this.socketStart){
				console.log('webSocket');
			}else{
				let userid=store.state.userid?store.state.userid:store.getters.userid;
				let url=this.socketUrl.replace("https://","wss://").replace("http://","ws://")+"/"+socket_type+"/"+userid+"_app";
				console.log("this.socketUrl:",url);
				
				uni.connectSocket({
					url: url,
					method: 'GET'
				});
				uni.onSocketOpen((res) => {
					this.socketStart = true;
					callback && callback();
					console.log('WebSocket');
				});
				/*setTimeout(() => {
				   _this.getHeartbeat();
				}, 5000);*/
			}
		}else{
			console.log('config/baseUrl socketUrl');
		}
	}
	//Socket
	send(data, callback) {
		const _this = this;
		if (store.state.userid) {
			data.userUid =store.state.userid;
		}
		console.log(data);
		uni.sendSocketMessage({
			data: JSON.stringify(data),
			success: () => {
				callback && callback(true);
			},
			fail: () => {
				callback && callback(false);
			}
		});
	}
	//Socket
	socketReceive() {
		const _this = this;
		uni.onSocketMessage(function(res) {
			console.log("APP:----:",res);
			let data = JSON.parse(res.data);
			//console.log('', data);
			_this.acceptMessage && _this.acceptMessage(data);
		});
	}
	//Socket
	closeSocket() {
		const _this = this;
		uni.closeSocket();
		_this.socketStart = false;
	}
	//Socket
	monitorSocketClose() {
		const _this = this;
		uni.onSocketClose(function(res) {
			console.log('WebSocket ');
			_this.socketStart = false;
			setTimeout(function() {
				//_this.init();
			}, 3000); 
		});
	}
	//Socket
	monitorSocketError() {
		const _this = this;
		uni.onSocketError(function(res) {
			_this.socketStart = false;
			console.log('WebSocket');
		});
	}
	//
	getHeartbeat() {
		const _this = this;
		this.send({
			type: "",
			userUid: store.state.userid
		}, (val) => {
			setTimeout(() => {
				if (val) {
					//_this.getHeartbeat();
				} else {
					if(!_this.socketStart){
						//_this.init();
					}
				}
			}, 10000);
		});
	}
};
const mySocket = new socket();
export default mySocket;
